import React, {useState} from 'react';
import Item from "./Item";
import {Line} from "react-chartjs-2";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

interface IItem {
    text: string;
    count: number | string;
    sub_text: string;
}

interface IMessage {
    bytes: number;
    consumers: number;
    messages: number;
    streams: number;
    time: number;
}

interface IData {
    type: string;
    message: IMessage;
}

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);

const Main = () => {

    const [graphic, setGraphic] = useState<IMessage[]>([]);
    const [step, setStep] = useState<number>(0);

    let sock: any = null;
    let wsuri = "ws://localhost:80/ws";

    window.onload = function() {

        console.log("onload");

        sock = new WebSocket(wsuri);

        sock.onopen = function() {
            console.log("connected to " + wsuri);
        }

        sock.onclose = function(e: any) {
            console.log("connection closed (" + e.code + ")");
        }

        sock.onmessage = function(e: any) {
            const dataMessage: IData = JSON.parse(e.data);
            if (dataMessage.type === 'statistic') {
                setStep(dataMessage.message.time)
                let graphics = graphic;
                graphics.push(dataMessage.message)
                setGraphic(graphics);
                let itemsData = [...items];
                itemsData[0].count = dataMessage.message.consumers;
                itemsData[1].count = dataMessage.message.streams;
                itemsData[2].count = dataMessage.message.messages;
                let bt: number | string = dataMessage.message.bytes;
                if (bt > 1024) {
                    bt = Math.round(bt / 1024) + ' kb';
                    if (parseInt(bt) > 1024) {
                        bt = Math.round(parseInt(bt) / 1024) + ' mb';
                    }
                }
                itemsData[3].count = bt;
                setItems(itemsData);
            }
        }
    }

    const times = graphic.map(function (graph, index: number) {
        let time = new Date(graph.time * 1000);
        let sec: string | number = time.getSeconds();
        if(sec < 10) {
            sec = '0' + sec;
        }
        //console.log(graphic.length)
        return (
            <div>
            <div className="circle_time"></div>
            {/*<div s='display: none' key={index}>{time.getHours()}:{time.getMinutes()}:{sec}</div>*/}
            </div>
        )
    });

    const ITEMS = [
        {text: 'Consumers', count: 2, sub_text: 'total consumers'},
        {text: 'Streams', count: 6, sub_text: 'total streams'},
        {text: 'Messages', count: 3, sub_text: 'total messages'},
        {text: 'Bytes', count: 24711, sub_text: 'total bytes'},
    ];

    const [items, setItems] = useState<IItem[]>(ITEMS);

    const itemsBlock = items.map(function (item: IItem, index) {
        return (
          <Item
              text={item.text}
              count={item.count}
              sub_text={item.sub_text}
              key={index.toString()}
          />
        );
    });

    //const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July'];
    const labels = graphic.map(function (message, index) {
        let date = new Date(message.time * 1000);
        let time = date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
        return time;
    })

    const data = {
        labels,
        datasets: [
            {
                label: 'bytes',
                data: graphic.map(function(message)  {
                    return message.bytes;
                }),
                borderColor: 'rgb(173,97,245)',
                backgroundColor: 'rgba(147,53,235,0.5)',
            },
            {
                label: 'streams',
                data: graphic.map(function(message)  {
                    return message.streams;
                }),
                borderColor: 'rgb(53, 162, 235)',
                backgroundColor: 'rgba(53, 162, 235, 0.5)',
            },
            {
                label: 'messages',
                data: graphic.map(function(message)  {
                    return message.messages;
                }),
                borderColor: 'rgb(53,235,77)',
                backgroundColor: 'rgba(53,235,111,0.5)',
            },
            {
                label: 'consumers',
                data: graphic.map(function(message)  {
                    return message.consumers;
                }),
                borderColor: 'rgb(235,211,53)',
                backgroundColor: 'rgba(235,150,53,0.5)',
            },
        ],
    };

    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top' as const,
            },
            title: {
                display: true,
                text: 'Chart.js Line Chart',
            },
        },
    };

    return (
        <div className='main'>
            <div className="grid">
                {itemsBlock}
            </div>
            <div className="new_messages">
                <div className="new_messages__title">New messages </div>
                <div className="new_messages__content">
                    <Line options={options} data={data} />
                </div>
            </div>
        </div>
    );
};

export default Main;